<template>
    <div>
      <el-button type="text" @click="dialogFormVisible = true"></el-button>
<el-dialog title="" :visible.sync="dialogFormVisible">
    <el-form :model="form" ref="form" :style="{ 'max-width': '800px' }">
        <el-form-item label="" prop="publish_category">
            <el-select v-model="form.publish_category" placeholder="">
               <el-option
              v-for="item in categories"
              :key="item.title"
              :label="item.title"
              :value="item.title">
            </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="magazine">
            <el-select v-model="form.magazine" placeholder="">
              <el-option
              v-for="item in filteredCategories"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="abstract">
          <el-input type="textarea" v-model="form.abstract" :style="{width: '600px'}" :rows="5"></el-input>
        </el-form-item>
       <el-form-item label=""  prop="authorname">
          <!-- <el-input v-model="form.authorname" autocomplete="off" prop="authorname"> </el-input> -->
          <el-select v-model="form.authorname"  placeholder="">
            <el-option
              v-for="item in options"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
        </el-select>
       </el-form-item>
       <el-form-item label="" prop="keywords">
          <el-input v-model="form.keywords" autocomplete="off"  style="width: 600px"> </el-input>
       </el-form-item>
       <el-form-item label="" prop="reviewer">
        <el-select v-model="form.reviewer" multiple placeholder="">
            <el-option
              v-for="item in options"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
        </el-select>
        </el-form-item>
    </el-form>
  <div class="hh">
    <el-upload
    class="upload-demo"
    drag
    action="#"
    :limit="1"
    :on-exceed="handleExceed"
    :auto-upload="false"
    :multiple="false"
    :before-upload="beforeAvatarUpload"
    ref="uploadPdf"
    :http-request="uploadFile"
    accept=".pdf"
    >
    <i class="el-icon-upload"></i>
    <div class="el-upload__text"><em></em></div>
    <!-- <el-button type="primary"></el-button> -->
    <div class="el-upload__tip" slot="tip">500MPDF</div>
    </el-upload>
  </div>
  <div slot="footer" class="dialog-footer">
    <el-button @click="resetForm('form')"> </el-button>
    <el-button type="primary" @click="addclick"> </el-button>
  </div>
</el-dialog>
    </div>
</template>
<script>
export default {
  name: 'Addresource',
  props: ['privateflag', 'categories'],
  data () {
    return {
      form: {
        //   name: '',
        publish_category: '',
        magazine: '',
        abstract: '',
        authorname: '',
        keywords: '',
        reviewer: []
      },
      file: {},
      dialogFormVisible: false,
      options: [],
      isLt2M: false
    }
  },
  computed: {
    filteredCategories () {
      console.log(this.categories.filter(category => category.title === this.form.publish_category))
      return this.form.publish_category === '' ? [''] : this.categories.filter(category => category.title === this.form.publish_category)[0].subTitle
    }
  },
  mounted () {
    this.$http.get('/api/find_user').then((res) => {
      this.options = res.data
      console.log('name:' + this.options)
    })
  },
  methods: {
    // resetForm () {
    //   this.form = {}
    //   this.isLt2M = false
    // },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.isLt2M = false
    },
    // submit()
    uploadFile (param) {
      this.file = param.file
    },
    // 
    handleExceed () {
      this.$alert('', {
        confirmButtonText: ''
      })
    },
    beforeAvatarUpload (file) {
      this.isLt2M = file.size / 1024 / 1024 < 500
      if (!this.isLt2M) {
        this.$message.error(' 500MB!')
      }
    },
    addclick () {
      this.$refs.uploadPdf.submit()
      console.log(':' + this.form.authorname)
      // 
      const formDatas = new FormData()
      if (this.isLt2M) {
        let reviewer = this.form.reviewer.join(';')
        // append
        formDatas.append('file', this.file) // 
        formDatas.append('publish_category', this.form.publish_category)
        formDatas.append('magazine', this.form.magazine)
        formDatas.append('abstract', this.form.abstract)
        formDatas.append('authors', this.form.authorname)
        formDatas.append('keywords', this.form.keywords)
        formDatas.append('reviewer', reviewer)
        // axios
        console.log(reviewer + '-------' + this.form.authorname)
        this.$http.post('/api/upload', formDatas).then((res) => {
          console.log(res.data)
          if (res.data) {
            console.log('seccess!')
            this.dialogFormVisible = false
          }
        }).catch(error => {
          console.log(error)
        })
        const formDatas2 = new FormData()
        // append
        formDatas2.append('receivers', reviewer) // 
        formDatas2.append('filename', '-' + this.file.name)
        formDatas2.append('author', this.form.authorname)
        // 
        this.$http.post('/api/sendmail', formDatas2).then((res) => {
          console.log(res.data)
          if (res.data) {
            console.log('seccess!')
            this.dialogFormVisible = false
          }
        }).catch(error => {
          console.log(error)
        })
      }
    }

  }
}
</script>
<style scoped>
.hh{
      /**/
    display: flex;
    align-items: center;
  /**/
    justify-content: center;
    /* background-color: rgb(192, 198, 198); */
}
</style>



