<template>
    <div>
      <el-button type="text" @click="dialogFormVisible = true"></el-button>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form" ref="form">
          <el-form-item label="">
          <el-select v-model="form.authorname"  placeholder="">
            <el-option
              v-for="item in options"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
         </el-form-item>
         <el-form-item label="">
          <el-input type="textarea" v-model="form.abstract" prop="abstract"></el-input>
       </el-form-item >
        </el-form>
        <div class="hh">
         <el-upload
    class="upload-demo"
    drag
    action="#"
    :limit="1"
    :on-exceed="handleExceed"
    :auto-upload="false"
    :multiple="false"
    ref="uploadPdf"
    :http-request="uploadFile"
    accept=".pdf"
    >
    <i class="el-icon-upload"></i>
    <div class="el-upload__text"><em></em></div>
    <!-- <el-button type="primary"></el-button> -->
    <div class="el-upload__tip" slot="tip">PDF</div>
    </el-upload>
  </div>
  <div slot="footer" class="dialog-footer">
    <el-button @click="resetForm"> </el-button>
    <el-button type="primary" @click="addclick"> </el-button>
  </div>
      </el-dialog>
    </div>
</template>
<script>
export default {
  name: 'Addtemplate',
  data () {
    return {
      form: {
        authorname: '',
        abstract: ''
      },
      file: {},
      dialogFormVisible: false,
      options: []
    }
  },
  mounted () {
    this.$http.get('/api/find_user').then((res) => {
      this.options = res.data
      console.log('name:' + this.options)
    })
  },
  methods: {
    resetForm () {
      this.form = {}
    },
    addclick () {
      this.$refs.uploadPdf.submit()
      console.log(':' + this.form.authorname)
      // 
      const formDatas = new FormData()
      formDatas.append('file', this.file) // 
      formDatas.append('abstract', this.form.abstract)
      formDatas.append('authors', this.form.authorname)
      this.$http.post('/api/upload_template', formDatas).then((res) => {
        console.log(res.data)
      })
      this.dialogFormVisible = false
      this.$emit('update-data') // 
    },
    // submit()
    uploadFile (param) {
      this.file = param.file
    },
    // 
    handleExceed () {
      this.$alert('', {
        confirmButtonText: ''
      })
    }

  }
}
</script>
<style scoped>
.hh{
      /**/
    display: flex;
    align-items: center;
  /**/
    justify-content: center;
    /* background-color: rgb(192, 198, 198); */
}
</style>



